<template>
    <div class="top-nav">
        <nav>
            <div class="logo" @click="goHome">
                <div class="avatar">
                    <img src="@/assets/avatar.jpg" />
                </div>
                <div class="info">
                    <span class="text">幻影旋风 | </span>
                    <span class="subtext">晓鸽工作室</span>
                </div>
            </div>
            <ul class="nav">
                <li class="item" :class="index == active ? 'active' : ''" v-for="(item, index) in list" :key="index" @click="clickNav (item, index)">
                    {{item.name}}
                </li>
            </ul>
            <div class="userinfo">
                <el-dropdown trigger="click">
                    <el-avatar style="cursor: pointer" :src="defaultAvatarSrc" :size="40"></el-avatar>
                    <el-dropdown-menu slot="dropdown" class="userDropdownMenu">
                        <el-dropdown-item>登录</el-dropdown-item>
                        <el-dropdown-item>注册</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </nav>
    </div>
</template>
<script>
export default {
    name: 'TopNav',
    props: {
        list: {
            type: Array,
            default () {
                return []
            }
        },
        active: {
            type: Number,
            default: 0
        }
    },
    data () {
        return {
            defaultAvatarSrc: require ('@/assets/default_avatar.png')
        }
        
    },
    methods: {
        clickNav (item, index) {
            this.$emit ('update:active', index)
            this.$emit ('select', item.url)
        },
        goHome () {
            location.href = '/'
        }
    }
}
</script>
<style lang="scss" scoped>
.top-nav {
    background: #11feae;
    height: 60px;
    width: 100%;
    box-sizing: border-box;
    nav {
        position: relative;
        line-height: 60px;
        color: #fff;
        width: 1140px;
        margin: auto;
        .logo {
            float: left;
            margin: 0 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            .avatar {
                height: 30px;
                width: 30px;
                margin-right: 10px;
                img {
                    height: 100%;
                    width: 100%;
                    border-radius: 50%;
                }
                margin-top: -10px;
            }
            .info {
                .text {
                    font-size: 22px;
                }
                .subtext {
                    font-size: 16px;
                    vertical-align: -3px;
                }
            }
            &:hover {
                cursor: pointer;
            }
        }
        .nav {
            margin-left: 50px;
            float: left;
            overflow: hidden;
            max-width: 1000px;
            list-style: none;
            margin-top: -3px;
            .item {
                margin-right: 10px;
                float: left;
                display: block;
                padding: 0 10px;
                min-width: 50px;
                text-align: justify;
                text-align-last: justify;
                &:hover {
                    cursor: pointer;
                    background: #02da91;
                }
                &.active {
                    border-bottom: 3px solid #02a76f;
                    color: #02a76f;
                }
            }
        }
        .userinfo {
            position: absolute;
            top: 10px;
            right: 0;
        }
        @media (max-width: 780px) {
            justify-content: space-around;
            .logo {
                .info {
                    .subtext {
                        display: none;
                    }
                }
            }
            .nav {
                display: none;
            }
        }
    }
}
.userDropdownMenu {
    margin-top: -15px !important;
}
</style>