import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/404',
    component: () => import('@/views/404/404.vue') ,
    name:'页面不存在',
    meta:`` 
  },
  {
    path: '/',
    component: () => import ('@/views/home/index.vue'),
    name: 'Home'
  },
  { path: '*', redirect: '/404', hidden: true,meta:`` },
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
